.main-header {
  position: relative;
  z-index: 2;
  margin: 10px 20px 0 20px;

  @media (min-width: @tablet-width) {
    display: flex;
    justify-content: space-between;
    align-items: center;

    margin: 24px 30px 0 30px;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
    margin: 0 auto;
    margin-top: 63px;
    margin-bottom: 0;
  }
}


.main-header__logo {
  text-decoration: none;
}

